@import url("https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    font-family: "Poppins", sans-serif;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.heading {
    font-size: 3rem;
    margin: 6vh;
    border: 7px dashed rgb(1, 17, 160);
    padding: 1vh 8vw;
    text-align: center;
    border-radius: 20px;
}

.cards-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 1300px;
    margin: 0 auto;
}

.card {
    width: 400px;
    height: max-content;
    margin: 1rem;
    padding: 1rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.3);
}

.tourImage {
    width: 380px;
    aspect-ratio: 1/1;
    object-fit: cover;
}

.tourInfo {
    margin: 20px 5px;
}

.tour-price {
    color: #1faa59;
    font-size: 1.3rem;
    font-weight: bold;
}

.tour-name {
    font-size: 1.5rem;
    font-weight: bold;
}

.readMore {
    color: #12b0e8;
    cursor: pointer;
}

.notButton {
    background-color: #b4161b21;
    margin-top: 18px;
    padding: 10px 80px;
    border: 1px solid #b4161b;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
}

.notButton:hover {
    background-color: red;
    color: white;
    transition: all 0.3s;
}

.refresh {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.btnRefresh {
    margin-top: 18px;
    background-color: #58dc67;
    border-radius: 10px;
    padding: 10px 80px;
    border: 1px solid white;
    cursor: pointer;
}

.btnRefresh:hover {
    background-color: #0b8c1a;
    color: white;
    transition: all 0.3s;
}
